@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;700;800&display=swap);
.hero {
  width: 100%;
  height: 95vh;
  display: flex;
  padding: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero__text {
  font-size: 2rem;
  font-weight: 200;
}
.hero__title {
  margin: 2rem 0;
  font-size: 5rem;
  font-weight: 700
}
.navbar {
  z-index: 2;
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding: 0.5rem 10px;
  font-size: 1.5rem;
  font-weight: 600;
  color: grey;
  background-color: white;
}
.charts-area {
  margin: auto;
  max-width: 800px;
  padding: 1rem;
}
.charts-area__title {
  width: 100%;
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 4rem;
  display: inline-flex;
  justify-content: space-between;
  align-items: baseline;
}
.chart-selector {
  position: relative;
  font-size: 0.8em;
  color: grey;
  cursor: pointer;
  min-width: 25%;
  text-align: right;
}

.chart-selector__chevron {
  transition: transform ease-in-out 0.3s
}
.chart-selector:hover .chart-selector__chevron {
  transform: rotate(90deg)
}
.chart-selector__chevron--open{
  transform: rotate(90deg)
}
.chart-selector__options {
  position: absolute;
  right: 0px;
  background: white;
  margin-top: 0.5rem;
  padding: 1rem 2rem;
  border: 1px solid var(--green);
  border-radius: 0.5rem;
}
.chart-selector__options__option {
  margin: 0.7rem 0;
  font-size: 1rem;
  font-weight: 400;
  text-align: right;
  white-space: nowrap;
  transition: color ease-in-out 0.1s;

}
.chart-selector__options__option:hover {
  color: #3EA060
}

.chart-selector__options__option--selected {
  color: #3EA060
}
@media screen and (max-width: 700px){
  .chart-selector__options {
    width: 95vw;
    padding: 1rem;
    border: none;
    border-radius: 0;
  }
  .chart-selector__options__option {
    font-size: 1.7rem;
    text-align: center;
    margin: 2rem 0
  }
}
