/************************************
 * OVERRIDE ANNOYING DEFAULT STYLES *
 ************************************/

body {
  margin: 0;
}
* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

/******************
 * GLOBAL CLASSES *
 ******************/
:root {
  --green: #3EA060;
  --yellow: #FFD400;
  --orange: #EE6352
}

.orange {
  color: #EE6352;
  color: var(--orange);
}

.header {
  font-family: 'Inter', sans-serif;
}
.text {
  font-family: 'Inter', sans-serif;

  /*font-family: 'Lato', sans-serif;*/
}
.highlighted{
  position:relative;
}
.highlighted:before{
  content:"";
  background-color: #FFD400;
  background-color: var(--yellow);
  width:90%;
  height:1em;
  position:absolute;
  transform: rotate(-3deg);
  z-index:-1;
  -webkit-filter:url(#marker-shape);
          filter:url(#marker-shape);
  left:-0.15em;
  top:0.1em;
  padding:0 0.25em;
}

.icon {
  margin: 0 0.5rem
}
.only-mobile {
  display: none;
}

/*******************************
 * MAKING IT MOBILE RESPONSIVE *
 *******************************/
@media screen and (max-width: 700px){
  :root {
    font-size: 62.5%;
  }
  .only-mobile {
    display: inherit;
  }
}
.footer-area {
  margin: 3rem auto;
  max-width: 800px;
  padding: 1rem;
  text-align: center;
  line-height: 2rem;
}
@media screen and (max-width: 700px){
  .footer-area {
    font-size: 1.5rem;
  }
}
